@font-face {
  font-family: 'Imperial Script';
  src: url(../font/ImperialScript-Regular.ttf);
}
@font-face {
  font-family: 'Broadcast Matter';
  src: url(../font/Broadcast_Matter.ttf);
}

*{
  margin: 0;
  padding: 0;
}

body {
  background-color: #17182f;
}

#tsparticles {
  position: relative;
}

#Main_page {
  width: 100%;
  height: 963px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
 
  h1 {
    font-family: 'Imperial Script';
    font-size: 120px;
    color: #fff;
  }

  .flower1 {
    position: absolute;
    top: 0;
    right: 0;
    transform: scale(-1,-1);
  }

  .flower2 {
    position: absolute;
    bottom: 0;
    left: 0;
  }
}

#Time_line {
  width: 100%;
  height: 963px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  position: relative;

  h1 {
    font-size: 120px;
    color: #fff;
    font-family: 'Imperial Script';
    position: absolute;
    top: 100px;
    left: 50%;
    transform: translateX(-50%);
  }
}
